/* mindmap */
.km-view {
  font-family: "STHeitiSC-Light", "STHeiti", "Hei", "Heiti SC",
    "Microsoft Yahei", Arial, sans-serif;
  -webkit-user-select: none;
  user-select: none;
  position: relative;
}
.km-view .km-receiver {
  position: absolute;
  left: -99999px;
  top: -99999px;
  width: 20px;
  height: 20px;
  outline: none;
  margin: 0;
}

.km-view image {
  cursor: zoom-in;
}

.km-image-viewer {
  position: fixed;
  z-index: 99999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.75);
}

.km-image-viewer .km-image-viewer-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  white-space: nowrap;
  overflow: auto;
}

.km-image-viewer .km-image-viewer-container::before {
  content: "";
  display: inline-block;
  height: 100%;
  width: 0;
  font-size: 0;
  vertical-align: middle;
}

.km-image-viewer .km-image-viewer-container img {
  cursor: zoom-out;
  vertical-align: middle;
}

.km-image-viewer .km-image-viewer-container img.limited {
  cursor: zoom-in;
  max-width: 100%;
  max-height: 100%;
}

.km-image-viewer .km-image-viewer-toolbar {
  z-index: 1;
  background: rgba(0, 0, 0, 0.75);
  text-align: right;
  transition: all 0.25s;
}

.km-image-viewer .km-image-viewer-toolbar.hidden {
  transform: translate(0, -100%);
  opacity: 0;
}

.km-image-viewer .km-image-viewer-btn {
  cursor: pointer;
  outline: 0;
  border: 0;
  width: 44px;
  height: 44px;
  background: url("data:image/svg+xml;base64,");
}

.km-image-viewer .km-image-viewer-toolbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.km-image-viewer .km-image-viewer-close {
  background-position: 0 -44px;
}

